@import url(https://fonts.googleapis.com/css2?family=Poppins:wght@500&display=swap);
@font-face {
  font-family: 'Poppins';
  src: local('Poppins'), url(/imdb_app_reactjs/static/media/Poppins-Regular.8081832f.ttf) format('opentype');
}

@font-face {
  font-family: 'Poppins Light Italic';
  src: local('Poppins'), url(/imdb_app_reactjs/static/media/Poppins-LightItalic.5ccd942a.ttf) format('opentype');
}

body {
  margin: 0;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

* {
  font-family: Poppins;
}

#root {
  background: linear-gradient(to top right, #993399 -127%, #000000 90%);
  min-height: 100vh;
  min-width: 100vw;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New', monospace;
}
.motto {
  text-align: center;
  padding-top: 30px;
  font-style: italic;
  font-size: 20px;
  color: lightgrey;
}
.carousel {
  margin: 20px auto;
}
.movie-container {
  /* background: linear-gradient(to top left, #808080 -127%, #000000 86%); */
  background: linear-gradient(to top left, #808080 -220%, #000000 79%);
  border-radius: 3px;
  color: white;
  min-height: 450px;
  max-height: auto;
  padding: 20px;
}

.movie-container:hover {
  cursor: pointer;
}

.category-name {
  color: white;
  font-size: 25px;
}

hr {
  background-color: white;
}

.movie-details-container {
  margin-top: 25px;
}

.movie-icons-container {
  padding: 5px;
}

.movie-detail {
  font-size: 20px;
  margin-left: 10px;
  display: inline-flex;
  align-items: center;
  vertical-align: middle;
}
*{
    margin: 0;
    padding: 0;
}

.mainSearchContainer{
    display: flex;
    align-items: flex-start;
}

.searchBox{
    background-color: white;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    color: purple;
    border-radius: 10px;
    font-weight: bold;
    font-size: 1.5em;
    padding: 20px;
    margin-bottom: 15px;
}
.searchBox p{
    font-weight: 600;
    margin-top: 5px;
}
.searchBox input{
    text-align: center;
}

.searchButton{
    margin-top: 15px;
    margin-bottom: 15px;
    color: purple;
    background-color: white;
    font-weight: bold;
}

.searchButton:hover{
    color: white;
    background-color: purple;
    font-weight: bold;
}

.movieHeading{
    color: white;
    font-weight: bold;
    text-align: center;
}

.moviesContainer{
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
    align-items: center;
    flex: 1 1;
}

.oneMovieContainer{
    height: 400px;
    width: 300px;
    display: flex;
    flex-direction: column;
    justify-content: flex-start;
    align-items: center;
    margin-bottom: 15px;
    background-color: white;
    border-radius: 5%;
    margin: 5px 5px;
    text-align: center;
    padding: 20px;
}
.oneMovieContainer img{
    width: 85%;
    height: 85%;
}

.oneMovieContainer p{
    font-weight: bold;
    color: purple;
}

.oneMovieContainer:hover{
    cursor: pointer;
}

.mainContainerSearch{
    display: flex;
    align-items: center;
    justify-content: center;
    flex: 1 1;
    flex-direction: column;
}

.form-control{
    margin: 10px 0px;
}
* {
    margin: 0;
    padding: 0;
    box-sizing: border-box;
}

.userData input {
    color: white;
    border-style: none;
    background-color: rgba(0, 0, 0, 0);
}

.userData p{
    color:azure;
}


.form-container{
    display: flex;
    align-items: center;
    flex-direction: column;
    border-radius: 5%;
    background-color: #ffffff24;
    margin: auto;
    width: 50%;
    padding: 100px;
    margin-top: 15px;
}

.form-nav{
    margin-top: 20px;
    width:50%;
    padding:10px;
}
.form-render{
    display: flex;
    justify-content: center;
    align-items: center;
    height: 63vh;

}
.form {
    padding: 100px;
}

.form input { 
    text-align: center; 
}

.form-footer{
    display: flex;
    justify-content: center;
    align-items: center;
    margin-top: auto;
    padding:100px;
}

:focus {
    outline: none;

}
.header {
  /* background: rgb(59, 32, 82);
  background: linear-gradient(90deg, rgba(59, 32, 82, 1) 50%, rgba(210, 200, 228, 1) 100%); */
  /* background: linear-gradient(to top right, #993399 -127%, #000000 90%); */
  background: linear-gradient(to top right, #993399 -127%, #000000 90%);
  padding: 10px auto;
  display: flex;
  position: -webkit-sticky;
  position: sticky;
  top: 0;
  z-index: 1;
}

.app-logo {
  width: auto;
  max-height: 75px;
}

.app-name {
  font-size: 20px;
  color: white;
  font-weight: bold;
  text-decoration: none;
  margin: 0;
}

.app-name:hover {
  text-decoration: none !important;
  transition-delay: 0.1s;
}

.logo-container, .appname-container {
  display: flex;
  flex-direction: column;
  justify-content: center;
}

.appname-container {
  padding-left: 0 !important;
}

.links-container {
  display: flex;
  justify-content: center;
  flex-direction: column;
}

.logout-container {
  display: flex;
  justify-content: center;
  flex-direction: column;
  padding-left: 0 !important;
}

.login-register {
  padding-right: 5px;
  text-align: right;
  color: white;
  text-decoration: none;
  margin: 0;
}

.login-register:hover {
  color: white;
  text-decoration: none;
}

.logout {
  padding-right: 5px;
  text-align: center;
  color: white;
  text-decoration: none;
  margin: 0;
  cursor: pointer;
}

.logout:hover {
  color: white;
  text-decoration: none;
}

.add-movie {
  padding-right: 5px;
  text-align: right;
  color: white;
  text-decoration: none;
  margin: 0;
}

.add-movie:hover {
  color: white;
  text-decoration: none;
}

.search-container {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  padding: 15px 10px 25px 10px;
}

.search-container input {
  width: 100%;
  padding: 5px 10px;
  border-radius: 15px;
  border-color: transparent;
  background-color: #d8c6e7;
  color: white;
}

.search-container input:focus {
  outline: none;
  border-color: transparent;
}

.search-container input::-webkit-input-placeholder {
  color: white;
}

.search-container input:-ms-input-placeholder {
  color: white;
}

.search-container input::placeholder {
  color: white;
}
.movie-details {
  display: flex;
  flex-direction: row;
  justify-content: space-around;
  width: 60px;
  height: 60px;
}

.movie-description {
  font-family: "Poppins", sans-serif;
  display: flex;
  flex-direction: column;
  justify-content: space-around;
  font-size: medium;
  list-style-type: none;
  background-color: #b6bdb41e;
  color: hsl(39, 66%, 57%);
  margin-left: 0px;
  margin-top: 5%;
  border-radius: 20px;
  width: 50vw;
  height: 40vh;
  padding-left: 2%;
}

.posters {
  display: flex;
  margin: 1%;
  margin-top: 20px;
  margin-bottom: 20px;
  margin-left: 0px;
  width: 35vw;
  height: 45vw;
  border-radius: 20px;
  display: flex;
  align-items: center;
}

.movie-title {
  margin-top: 5%;
  color: aliceblue;
  font-weight: 900;
  padding: 2%;
  font-size: 30px;
  font-family: "Poppins", sans-serif;
}

.loading {
  color: aliceblue;
  font-size: 30px;
  text-align: center;
  padding-top: 30px;
}

.button {
  margin-top: 20px;
  padding: 5px;
  width: 100px;
  height: 40px;
  border-radius: 50px;
  border-style: none;
  margin-left: 5%;
  background-color: #b6bdb41e;
  color: hsl(39, 66%, 57%);
}

.button-del {
  margin-top: 20px;
  padding: 5px;
  width: 130px;
  height: 40px;
  border-radius: 50px;
  border-style: none;
  margin-left: 65%;
  background-color: #b6bdb41e;
  color: hsl(39, 66%, 57%);
}

.imdbRating {
  color: hsl(0, 0%, 96%);
  margin-top: 10%;
  padding-top: 3%;
  padding-left: 5%;
}

.imdbRating .rating {
  color: hsl(39, 66%, 57%);
  font-size: 30px;
}

.movie-des {
  font-size: 17px;
  padding-left: 10px;
  color: rgb(202, 202, 202);
}

.movie-des-plot {
  font-size: 15px;
  padding-left: 10px;
  color: rgb(202, 202, 202);
}

.exit {
  display: flex;
  justify-content: center;
  background-color: #b6bdb41e;
  color: hsl(39, 66%, 57%);
  border-style: none;
  border-radius: 50px;
  font-size: 20px;
  padding-top: 5px;
  margin-top: 2%;
  width: 40px;
  height: 40px;
}



